img {
  max-width: 100%; }

.button {
  display: inline-block;
  background-color: #fa6900;
  border-radius: 4px;
  padding: 10px 20px;
  color: white;
  font-size: 15px;
  font-weight: 400;
  text-transform: uppercase;
  cursor: pointer; }
  .button:hover {
    background: linear-gradient(to top, rgba(17, 17, 17, 0.1), rgba(17, 17, 17, 0.1)), #fa6900; }

.header__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fa6900; }
  .header__wrapper.header--landing {
    background-color: transparent; }

.nav__wrapper {
  margin: 0 auto;
  max-width: 960px;
  padding: 10px 0; }
  .nav__wrapper:after {
    content: "";
    display: table;
    clear: both; }

.nav.nav--left {
  float: left; }

.nav.nav--right {
  float: right; }

.nav__link {
  display: inline-block;
  margin: 0 14px;
  line-height: 54px;
  color: white;
  font-size: 15px;
  font-weight: 500;
  text-decoration: none; }

.content__wrapper {
  margin: 0 auto;
  max-width: 960px;
  background-color: #e5e5dc; }

.content__section {
  padding: 24px 12px; }

.landing__wrapper {
  display: flex;
  align-items: center;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.1), transparent), linear-gradient(to top, rgba(50, 50, 50, 0.75), rgba(50, 50, 50, 0.75)), url("/static/img/student.jpg");
  background-position: center center;
  background-size: cover;
  padding: 88px 0;
  height: 600px;
  color: white;
  text-align: center; }
  .landing__wrapper h1 {
    margin: 0 auto 10px auto;
    max-width: 800px;
    font-size: 4em; }
  .landing__wrapper p {
    margin: 0 auto 24px auto;
    max-width: 600px;
    line-height: 24px;
    font-size: 18px;
    font-weight: 300; }
  .landing__wrapper .landing__content {
